import 'tippy.js/dist/tippy.css'

import React from 'react'
import { Token, Typeahead } from 'react-bootstrap-typeahead'
import { Option } from 'react-bootstrap-typeahead/types/types'
import Card from 'react-bootstrap/Card'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import Form from 'react-bootstrap/Form'
import Navbar from 'react-bootstrap/Navbar'
import Row from 'react-bootstrap/Row'
import tippy from 'tippy.js'

import Calendar from './Calendar'
import { getHolidaysForYear, listAllPlacesAvailable, YearsWorthOfHoliday } from './logic'

const deepEqual = require('deep-equal')
const COLORS = [
  '#3cb44b',
  '#4363d8',
  '#f58231',
  '#f032e6',
  '#fabebe',
  '#008080',
  '#e6beff',
  '#9a6324',
  '#fffac8',
  '#bcf60c',
  '#800000',
  '#ffe119',
  '#46f0f0',
  '#aaffc3',
  '#808000',
  '#ffd8b1',
  '#000075',
  '#808080',
  '#911eb4',
  '#e6194b',
]

const options = listAllPlacesAvailable()

let defaults = [{ country: 'US' }, { country: 'GB', state: 'SCT' }, { country: 'PT' }, { country: 'RO' }]
if (window.location.hash.startsWith('#c=')) {
  defaults = window.location.hash
    .substring(3)
    .split(',')
    .map((c) => {
      if (c.includes('-')) return { country: c.split('-')[0], state: c.split('-')[1] }
      return { country: c }
    })
}
const defaultSelection = options.filter((o) => defaults.find((d) => deepEqual(o.value, d)))

const shortDate = (date: Date) => {
  var options: Intl.DateTimeFormatOptions = {
    weekday: 'short',
    month: 'short',
    day: 'numeric',
  }
  return new Intl.DateTimeFormat('en-GB', options).format(date)
}

const HolidayList = ({ country, countryCode, holidays }: { country: string; countryCode: string; holidays: any }) => {
  return (
    <Col>
      <Card>
        <Card.Header>
          <img src={`/flags/${countryCode}.svg`} width={30} height={30} alt="flag" />{' '}
          <strong style={{ verticalAlign: 'middle' }}>{country}</strong>
        </Card.Header>
        <Card.Body>
          <ul>
            {holidays.map((h: any) => {
              return (
                <li key={h.id}>
                  <>
                    {shortDate(h.startDate)}: {h.name}
                  </>
                </li>
              )
            })}
          </ul>
        </Card.Body>
      </Card>
    </Col>
  )
}

let tooltip: any = null

function App() {
  const [countrySelection, setCountrySelection] = React.useState<Option[]>(defaultSelection)
  const [year, setYear] = React.useState(new Date().getFullYear())
  const [holidaysThisYear, setHolidaysThisYear] = React.useState<YearsWorthOfHoliday>({ all: [], places: [] })
  const [includeAllTypes, setIncludeAllTypes] = React.useState(false)

  React.useEffect(() => {
    const hashStuff = countrySelection.map((c: any) => {
      return c.value.state ? c.value.country + '-' + c.value.state : c.value.country
    })
    window.location.hash = 'c=' + hashStuff

    let colorIndex = 0
    const placesToCover = countrySelection.map((c: any) => {
      return { ...c.value, color: COLORS[colorIndex++ % COLORS.length] }
    })
    setHolidaysThisYear(getHolidaysForYear(year, includeAllTypes, placesToCover))
  }, [year, countrySelection, includeAllTypes])

  const showHolidayAssociatedWithDate = (e: any) => {
    if (e.events.length > 0) {
      let popupMessage = ''

      for (var i in e.events) {
        const ev = e.events[i]
        popupMessage += `<span style="color:${ev.color}">${ev.country}</span>: ${ev.name} <br />`
      }

      if (tooltip !== null) {
        tooltip.destroy()
        tooltip = null
      }

      tooltip = tippy(e.element, {
        content: popupMessage,
        placement: 'bottom',
        animation: 'shift-away',
        arrow: true,
        allowHTML: true,
      })
      tooltip.show()
    }
  }

  return (
    <>
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="/">
            <img
              alt=""
              src="data:image/png;base64,"
              width="30"
              height="30"
              className="d-inline-block align-top"
              style={{ marginRight: '0.5em' }}
            />
            Every Public Holiday
          </Navbar.Brand>
        </Container>
      </Navbar>

      <Container>
        <div style={{ marginTop: '0.7em' }}>
          <Form.Group style={{ marginBottom: '1em' }}>
            <Typeahead
              id="country-chooser"
              labelKey="label"
              multiple
              onChange={setCountrySelection}
              options={options}
              placeholder="Choose a country"
              selected={countrySelection}
              clearButton
              renderToken={(option: any, props, index) => {
                return (
                  <Token
                    key={option.label}
                    option={option}
                    disabled={props.disabled}
                    onRemove={props.onRemove}
                    style={{ color: 'white', backgroundColor: COLORS[index % COLORS.length] }}
                  >
                    {option.label}
                  </Token>
                )
              }}
            />
          </Form.Group>

          <Calendar
            year={year}
            language="en"
            weekStart={1}
            dataSource={holidaysThisYear.all}
            onDayEnter={showHolidayAssociatedWithDate}
            onYearChanged={({ currentYear }: any) => {
              setYear(currentYear)
            }}
          />
          <Form.Check
            className="mt-2"
            type="switch"
            id="custom-switch"
            label="Include other observed days"
            onChange={(e) => {
              setIncludeAllTypes(e.target.checked)
            }}
          />
        </div>

        <Row xs={1} md={2} className="g-4 mt-2">
          {holidaysThisYear.places.map((h) => {
            return <HolidayList key={h.country} country={h.country} countryCode={h.countryCode} holidays={h.holidays} />
          })}
        </Row>
      </Container>
      <footer className="mt-4 text-center text-lg-start bg-light text-muted">
        <div className="text-center p-4" style={{ backgroundColor: 'rgba(0, 0, 0, 0.05)' }}>
          Holiday date updated weekly from{' '}
          <a className="text-reset fw-bold" href="https://github.com/commenthol/date-holidays">
            date-holidays
          </a>{' '}
          - some countries are missing, help them out.
        </div>
      </footer>
    </>
  )
}

export default App
